      "poptipCode3": '\n        &lt;template>\n           &lt;Poptip v-model="visible">\n               &lt;a>click \u6FC0\u6D3B&lt;/a>\n               &lt;div slot="title">\n                   &lt;i>\u81EA\u5B9A\u4E49\u6807\u9898&lt;/i>\n               &lt;/div>\n               &lt;div slot="content">\n                   &lt;a @click="close">\u5173\u95ED\u63D0\u793A\u6846&lt;/a>\n               &lt;/div>\n           &lt;/Poptip>\n        &lt;/template>\n        &lt;script>\n            export default {\n              data () {\n                return {\n                  visible: false\n                }\n              },\n              methods: {\n                  close () {\n                      this.visible = false;\n                  }\n              }\n            }\n        &lt;/script>\n      ',
